// src/app/cart/page.js
'use client'

import React from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { Trash2, Minus, Plus, ChevronLeft, ShoppingBag } from 'lucide-react'
import { useCart } from '@/context/CartContext'
import productsData from '@/datas/Products.json'

import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { Separator } from '@/components/ui/separator'
import Navbar from '@/components/home/Navbar' // Navbarni chaqiramiz

export default function CartPage() {
	const {
		cartItems,
		increaseCartQuantity,
		decreaseCartQuantity,
		removeFromCart,
	} = useCart()

	// Savatdagi IDlarga mos mahsulotlarni topamiz va sonini (quantity) qo'shamiz
	const cartProducts = cartItems.map(cartItem => {
		const item = productsData.find(p => p.id === cartItem.id)
		return {
			...item,
			quantity: cartItem.quantity,
		}
	})

	// --- HISOB-KITOB QISMI ---
	const totalPrice = cartProducts.reduce(
		(total, item) => total + item.newPrice * item.quantity,
		0
	)

	const totalOldPrice = cartProducts.reduce(
		(total, item) => total + item.oldPrice * item.quantity,
		0
	)

	const totalSavings = totalOldPrice - totalPrice

	const formatPrice = price => {
		return new Intl.NumberFormat('uz-UZ').format(price)
	}

	// Agar savat bo'sh bo'lsa
	if (cartItems.length === 0) {
		return (
			<div className='min-h-screen bg-gray-50 pb-20'>
				<Navbar />
				<div className='flex flex-col items-center justify-center h-[60vh] text-center px-4'>
					<div className='bg-gray-200 p-6 rounded-full mb-4'>
						<ShoppingBag className='w-16 h-16 text-gray-400' />
					</div>
					<h1 className='text-2xl font-bold text-gray-800 mb-2'>
						Savatingiz hozircha bo‘sh
					</h1>
					<p className='text-gray-500 mb-6'>
						Mahsulotlarni tanlash uchun bosh sahifaga o'ting
					</p>
					<Link href='/'>
						<Button className='bg-orange-500 hover:bg-orange-600 text-white px-8'>
							Xarid qilishga o'tish
						</Button>
					</Link>
				</div>
			</div>
		)
	}

	return (
		<div className='min-h-screen bg-gray-50 pb-20'>
			<Navbar />

			<div className='container mx-auto px-4 py-8'>
				<h1 className='text-2xl font-bold mb-6 flex items-center gap-2'>
					Savat{' '}
					<span className='text-gray-400 text-lg font-normal'>
						{cartItems.length} ta mahsulot
					</span>
				</h1>

				<div className='flex flex-col lg:flex-row gap-6'>
					{/* --- CHAP TOMON: MAHSULOTLAR RO'YXATI --- */}
					<div className='flex-1 space-y-4'>
						<Card className='border-none shadow-sm'>
							<CardContent className='p-0'>
								{cartProducts.map((item, index) => (
									<div key={item.id}>
										<div className='p-4 flex flex-col sm:flex-row gap-4'>
											{/* Rasm va Checkbox */}
											<div className='flex items-start gap-4'>
												<div className='relative w-24 h-24 sm:w-28 sm:h-28 bg-gray-50 rounded-lg overflow-hidden border'>
													<Image
														src={item.images[0].imageUrl}
														alt={item.nomi}
														fill
														className='object-contain p-2 mix-blend-multiply'
													/>
												</div>
											</div>

											{/* Ma'lumotlar */}
											<div className='flex-1 flex flex-col justify-between'>
												<div className='flex justify-between items-start gap-2'>
													<h3 className='font-medium text-gray-800 line-clamp-2 text-sm sm:text-base'>
														{item.nomi}
													</h3>
													<button
														onClick={() => removeFromCart(item.id)}
														className='text-gray-400 hover:text-red-500 transition p-1'
														title="O'chirish"
													>
														<Trash2 className='w-5 h-5' />
													</button>
												</div>

												<div className='flex flex-col sm:flex-row sm:items-end justify-between mt-4 gap-4'>
													{/* Plus / Minus tugmalari */}
													<div className='flex items-center border border-gray-300 rounded-lg w-fit'>
														<button
															onClick={() => decreaseCartQuantity(item.id)}
															className='p-2 text-gray-500 hover:bg-gray-100 disabled:opacity-50'
														>
															<Minus className='w-4 h-4' />
														</button>
														<span className='w-10 text-center font-semibold text-gray-800'>
															{item.quantity}
														</span>
														<button
															onClick={() => increaseCartQuantity(item.id)}
															className='p-2 text-gray-500 hover:bg-gray-100'
														>
															<Plus className='w-4 h-4' />
														</button>
													</div>

													{/* Narxlar */}
													<div className='text-right'>
														{item.quantity > 1 && (
															<p className='text-xs text-gray-400 mb-1'>
																{item.quantity} dona uchun
															</p>
														)}
														<p className='font-bold text-lg text-gray-900'>
															{formatPrice(item.newPrice * item.quantity)} so'm
														</p>
														<p className='text-sm text-gray-400 line-through'>
															{formatPrice(item.oldPrice * item.quantity)} so'm
														</p>
													</div>
												</div>
											</div>
										</div>
										{/* Chiziq, oxirgi elementdan tashqari */}
										{index !== cartProducts.length - 1 && <Separator />}
									</div>
								))}
							</CardContent>
						</Card>
					</div>

					{/* --- O'NG TOMON: CHECKOUT (RASMIYLASHTIRISH) --- */}
					<div className='lg:w-[350px] space-y-4'>
						<Card className='border-none shadow-sm sticky top-24'>
							<CardContent className='p-6'>
								<h2 className='text-lg font-bold mb-4'>Buyurtmangiz</h2>

								<div className='space-y-3 text-sm'>
									<div className='flex justify-between text-gray-600'>
										<span>
											Mahsulotlar (
											{cartItems.reduce((a, b) => a + b.quantity, 0)}):
										</span>
										<span>{formatPrice(totalOldPrice)} so'm</span>
									</div>
									<div className='flex justify-between text-green-600 font-medium'>
										<span>Tejab qolindi:</span>
										<span>- {formatPrice(totalSavings)} so'm</span>
									</div>
									<Separator className='my-2' />
									<div className='flex justify-between items-center'>
										<span className='font-bold text-lg'>Jami:</span>
										<div className='text-right'>
											<span className='block font-bold text-xl text-blue-900'>
												{formatPrice(totalPrice)} so'm
											</span>
										</div>
									</div>
								</div>

								<Button className='w-full bg-orange-500 hover:bg-orange-600 text-white font-bold mt-6 py-6 text-base'>
									Rasmiylashtirishga o'tish
								</Button>

								<p className='text-xs text-gray-400 mt-4 text-center'>
									Tugmani bosish orqali foydalanish shartlarini qabul qilasiz.
								</p>
							</CardContent>
						</Card>
					</div>
				</div>
			</div>
		</div>
	)
}
